.header-content {
  overflow: hidden;
  background-color: #519872;
  position: fixed;
  top: 0;
  width: 100%;
  height: 75px;
  color: white;

  z-index: 1;
}

.header-content h1 {
  color: #ffffff;
  left: 445px;
  font-weight: 700;
  font-size: 30px;
  position: absolute;
  display: block;
}

.header-content img {
  top: 23px;
  position: absolute;
  left: 400px;
}

.house {
  width: 100%;
  height: 160px;
  background-color: #dedede;
  margin: 30px 0;
  position: relative;
  padding-bottom: 10px;
}

.house_details {
  height: 100%;
  width: 33%;
  /* padding: 10px; */
  padding-top: 10px;
  display: -webkit-flex;
  display: flex;
  float: left;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.house_details * {
  margin: 0 0 10px 0;
}

.house_details p {
  display: block;
  margin: 0;
  font-weight: lighter;
  padding: 8px 0 7px 35px;
  font-family: "Open Sans", sans-serif;
  /* margin-block-start: 1em; */
  /* margin-block-end: 1em; */
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.dash_container {
  /* border-bottom: 1px solid black; */
  margin: 0 20px 10px 20px;
}

.house_delele_button {
  position: absolute;
  top: 5px;
  right: 5px;
}

.house_delele_button:hover {
  cursor: pointer;
}

.house_img {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  margin-left: 12px;
  padding-left: 5px;
}

.wizard-content {
  width: 50vw;
  background-color: #e5f4ec;
  position: relative;
}

.wizard-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.wizard-heading {
  font-size: 30px;
  margin: 20px;
  font-weight: 700;
}

.wizard-header-button {
  margin: 20px;
  border: none;
  padding: 7px;
  font-size: 15px;
  font-weight: 700;
  background-color: #fec2c2;
}

.wizard-input-container {
  margin: 20px auto;
  width: 70%;
}

.wizard-input-property {
  margin: 10px;
  float: left;
  font-size: 18px;
  font-weight: 700;
}

.wizard-input-property * {
  margin: 2px;
}

.wizard-input-property p {
  margin-left: 12px;
}

.wizard-input-property input {
  border: 1px solid #519872;
  border-radius: 2px;
  height: 25px;
  display: inline-block;
  text-align: start;
}
.wizard-input-property input:focus {
  outline: none;
}

.wizard_button {
  margin: 20px;
  background-color: #3b5249;
  padding: 10px;
  color: white;
  font-size: 20px;
  position: absolute;
  bottom: 60px;
  right: 30px;
}

.wizard-img {
  margin: 10px;
  float: left;
  font-size: 18px;
  font-weight: 700;
}
.wizard-img p {
  margin-left: 12px;
}

.wizard-img * {
  margin: 2px;
}

.wizard-img input {
  border: 1px solid #519872;
  border-radius: 2px;
  height: 25px;
}

.wizard-previous {
  left: 30px;
  background-color: #3b5249;
  color: white;
  position: absolute;
  bottom: 60px;
  left: 30px;
}

.wizard-input-container {
  margin: 20px auto;
  width: 70%;
}

.wizard-recommended {
  text-align: center;
  font-weight: 700;
  margin: 20px;
}

.wizard-input input {
  border: 1px solid #519872;
  border-radius: 2px;
  height: 25px;
}

.wizard-input * {
  margin: 2px;
}
